/* eslint-disable flowtype/require-valid-file-annotation */

import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import {connect} from 'react-redux'
import Paper from 'material-ui/Paper'
import IconButton from 'material-ui/IconButton';
import LoadIcon from 'material-ui-icons/FileDownload';
import SaveIcon from 'material-ui-icons/FileUpload';
import Typography from 'material-ui/Typography'

const styles = theme => ({
    root: {
        width: '100%',
    },
})

const MyStore = (props) => <div style={{maxWidth: 1080, margin: 15}}><Paper className={props.classes.root} style={{paddingTop: 15}}>
    <div style={{marginLeft: 15, marginRight: 15}}><Typography type="caption">
        <b>All data stored only in local storage of your browser</b>, <br/>
        i.e. if you change computer, uninstall browser, etc then you lost your "My data".<br/>
        To prevent this situation you can save "My data" to file. Now, You can restore "My data" any time
    </Typography></div>
    <div style={{display: "flex", alignItems: "center", marginLeft: 15, marginBottom: 15}}>
        <div style={{display: "flex", alignItems: "center",width: "50%"}}>
            <Typography type="caption">Save my data</Typography>
            <IconButton onClick={props.onSave}>
                <LoadIcon/>
            </IconButton>
        </div>
        <div style={{display: "flex", alignItems: "center",width: "50%"}}>
            <Typography type="caption">Restore my data</Typography>
            <IconButton onClick={props.onLoad}>
                <SaveIcon/>
            </IconButton>
        </div>
    </div>
</Paper></div>

MyStore.propTypes = {
    classes: PropTypes.object.isRequired,
    onSave: PropTypes.func.isRequired,
    onLoad: PropTypes.func.isRequired,
}

// TODO: remove redux, not needed
export default connect(
    state => ({}),
    dispatch => ({})
)(withStyles(styles)(MyStore))


