import React from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import keycode from 'keycode';
import Table, {
    TableBody,
    TableCell,
    TableFooter,
    TableHead,
    TablePagination,
    TableRow,
    TableSortLabel,
} from 'material-ui/Table';
import Toolbar from 'material-ui/Toolbar';
import Typography from 'material-ui/Typography';
import Paper from 'material-ui/Paper';
import Checkbox from 'material-ui/Checkbox';
import Tooltip from 'material-ui/Tooltip';
import UpdateIcon from 'material-ui-icons/Update'
import Button from 'material-ui/Button'
//import FilterListIcon from 'material-ui-icons/FilterList';
import TextField from 'material-ui/TextField';
import { CircularProgress } from 'material-ui/Progress';
//import CheckIcon from 'material-ui-icons/Check';
//import ErrorIcon from 'material-ui-icons/Error';

class EnhancedTableHead extends React.Component {
    static propTypes = {
        numSelected: PropTypes.number.isRequired,
        onRequestSort: PropTypes.func.isRequired,
        onSelectAllClick: PropTypes.func,
        order: PropTypes.string.isRequired,
        orderBy: PropTypes.string.isRequired,
        rowCount: PropTypes.number.isRequired,
        columnData: PropTypes.array.isRequired
    };

    createSortHandler = property => event => {
        this.props.onRequestSort(event, property);
    };

    render() {
        const { onSelectAllClick, order, orderBy,/* numSelected, rowCount */} = this.props
        return (
            <TableHead>
                <TableRow>
                    {onSelectAllClick && <TableCell padding="checkbox">
                        {/*<Checkbox style={{opacity: rowCount ? 1:0, transition:"opacity",transitionDuration:"1s"}}*/}
                            {/*indeterminate={numSelected > 0 && numSelected < rowCount}*/}
                            {/*checked={numSelected === rowCount}*/}
                            {/*onChange={onSelectAllClick}*/}
                        {/*/>*/}
                    </TableCell>}
                    {this.props.columnData.map(column => {
                        return (
                            <TableCell
                                key={column.id}
                                numeric={column.numeric}
                                padding={column.disablePadding ? 'dense' : 'default'}
                                sortDirection={orderBy === column.id ? order : false}
                            >
                                <Tooltip
                                    title="Sort"
                                    placement={column.numeric ? 'bottom-end' : 'bottom-start'}
                                    enterDelay={300}
                                >
                                    <TableSortLabel
                                        active={orderBy === column.id}
                                        direction={order}
                                        onClick={this.createSortHandler(column.id)}
                                    >
                                        <b style={{fontSize: 14}}>{column.label}</b>
                                    </TableSortLabel>
                                </Tooltip>
                            </TableCell>
                        );
                    })}
                </TableRow>
            </TableHead>
        );
    }
}

const toolbarStyles = theme => ({
    root: {
        paddingRight: 2,
        paddingTop: 15,
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.primary.A700,
                backgroundColor: theme.palette.primary.A100,
            }
            : {
                color: theme.palette.primary.A100,
                backgroundColor: theme.palette.primary.A700,
            },
    spacer: {
        flex: '1 1 100%',
    },
    actions: {
        color: theme.palette.text.primary,
        width: "100%",
    },
    title: {
        flex: '0 0 auto',
    },
    progress: {
        position: 'absolute',
        top: -2,
        left: -2,
    },
});

let EnhancedTableToolbar = props => {
    const { selected, classes, title, subtitle, onSearch, onUpdate, updating, selectionToolbar, onDrawTitle } = props;
    const numSelected = selected ? Object.values(selected).filter(row=>row).length : 0
    return (
        <Toolbar
            className={classNames(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >
            <div className={classes.title}>
                {numSelected > 0 ? (
                    <Typography type="subheading">{numSelected} selected</Typography>
                ) : (
                    <Typography type="title">{onDrawTitle ? onDrawTitle(title) : title}</Typography>
                )}
            </div>
            {!numSelected && <div style={{textAlign:"center",width:"100%"}}>{onSearch && <TextField
                placeholder="Search"
                defaultValue=""
                InputLabelProps={{
                    shrink: true,
                }}
                onKeyUp={(event)=>{
                    if(this.searchTask) clearTimeout(this.searchTask)

                    if(event.keyCode===27) event.target.blur()
                    else if(event.keyCode===13) onSearch(event.target.value)
                    else {
                        this.searchTask = setTimeout((props,value) =>
                            onSearch(value), 1000, props, event.target.value)
                    }
                }}
            />}</div>}
            <div className={classes.actions}>
                {numSelected > 0 ? selectionToolbar(props) : (
                    onUpdate && <div style={{marginRight:15,whiteSpace:"nowrap",fontSize:"80%",display: "flex",alignItems:"center"}}>
                        <div>{subtitle}</div>
                        <div style={{position: "relative",marginLeft: 15}}>
                            <Button fab color="primary" disabled={updating}
                                    onClick={onUpdate}>
                                <UpdateIcon/>
                            </Button>
                            {updating && <CircularProgress size={60} className={classes.progress}/>}
                        </div>
                    </div>
                )}
            </div>
        </Toolbar>
    );
};

EnhancedTableToolbar.propTypes = {
    classes: PropTypes.object.isRequired,
    selected: PropTypes.object,
    title: PropTypes.string.isRequired,
    subtitle: PropTypes.string,
    onUpdate: PropTypes.func,
    onSearch: PropTypes.func,
    updating: PropTypes.bool,
    selectionToolbar: PropTypes.func
};

EnhancedTableToolbar = withStyles(toolbarStyles)(EnhancedTableToolbar);

const styles = theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing.unit * 3,
    },
    table: {
        minWidth: 400,
    },
    tableWrapper: {
        overflowX: 'auto',
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: "white",//theme.palette.primary.A700,
                backgroundColor: theme.palette.primary.A100,
            }
            : {
                color: "white",//theme.palette.primary.A100,
                backgroundColor: theme.palette.primary.A700,
            },
});

class EnhancedTable extends React.Component {
    sort = (data, orderBy, order, pinned) => order === "desc"
        ? data.sort((a, b) => {
            if (!pinned[b.id] && pinned[a.id]) return -1
            if (pinned[b.id] && !pinned[a.id]) return 1
            return b[orderBy] < a[orderBy] ? -1 : 1
        })
        : data.sort((a, b) => {
            if (!pinned[b.id] && pinned[a.id]) return -1
            if (pinned[b.id] && !pinned[a.id]) return 1
            return b[orderBy] > a[orderBy] ? -1 : 1
        })

    constructor(props, context) {
        super(props, context);
        const order = props.order!==undefined?props.order:"desc"
        const orderBy = this.props.orderBy
        this.state = {
            orderBy: orderBy ? orderBy : props.columnData[0].id,
            order: order,
            data: this.sort(props.data, props.orderBy, props.order, props.pinned ? props.pinned : {}),
            editId: undefined,
            updating: false,
            search: undefined,
            toSort: undefined, // {orderBy,order}
        };
    }

    componentWillReceiveProps(nextProps) {
        const {orderBy, order } = this.state
        if(this.props.data!==nextProps.data) { // new data
            this.setState({
                toSort: { orderBy, order },          // delayed sort
                updating: false, // drop updating flag any case
            })
            return
        }
        if( this.props.pinned!==nextProps.pinned) this.setState({toSort: { orderBy, order }})
    }

    componentDidUpdate(prevProps, prevState) {
        if(this.state.toSort) {
            setTimeout((self) => { // delayed sort
                if(!this.state.toSort) return // sorted already
                const {orderBy, order, requested} = this.state.toSort
                const _toSort = this.state.toSort
                const data = self.sort(self.props.data, orderBy, order, self.props.pinned ? self.props.pinned : {})
                self.setState({ data, orderBy, order, toSort: undefined })
                if(requested && this.props.onSorted) this.props.onSorted(_toSort)
            }, 200, this)
        }
    }

    handleRequestSort = (event, property) => {
        const { order } = this.state
        const _toSort = {
            orderBy: property, order: order === "desc" ? "asc" : "desc", requested: true
        }
        this.setState({toSort: _toSort})
    };

    handleKeyDown = (event, id) => {
        if (keycode(event) === 'space') {
            this.handleSelect(event, id);
        }
    };

    handleSelect = (event, row) => {
        //const selected = this.props.selected && this.props.selected[row.id] ? this.props.selected[row.id] : undefined
        if(!this.props.selected) {
            this.props.onSelect({
                [row.id]: row, // toggle row selection
            })
            return
        }

        this.props.onSelect(!event.target.checked
            ? Object.entries(this.props.selected)
                .filter(([id]) => id !== row.id)
                .reduce((acc,[id,value])=>({...acc,[id]: 1}),{})
            : {...this.props.selected, [row.id]: 1})
    };

    isSelected = id => this.props.selected!==undefined && this.props.selected[id] !== undefined;

    render() {
        const {
            onDrawTitle,
            disableSearch,
            pinned,
            classes,
            title,
            onUpdate,
            onEdit, subtitle, rowsPerPageOptions, selectionToolbar, selected, onSelect, onStylizeRow
        } = this.props;
        const { order, orderBy, editId, updating, toSort } = this.state;

        const data = this.state.search
            ? this.state.data.filter(coin => coin.name.toLowerCase().indexOf(this.state.search)!==-1 || coin.id.indexOf(this.state.search)!==-1)
            : this.state.data

        const page = this.props.page!==undefined ? this.props.page : 0
        const rowsPerPage = this.props.rowsPerPage!==undefined ? this.props.rowsPerPage : 10
        const emptyRows = rowsPerPage - Math.min(rowsPerPage, data.length - page * rowsPerPage);
        const numSelected = selected ? Object.values(selected).filter(row=>row).length : 0
        const dataLength =  this.props.data.length

        return (
            <Paper className={classes.root}>
                <EnhancedTableToolbar selected={selected} title={title} subtitle={subtitle} onDrawTitle={onDrawTitle}
                                      onUpdate={onUpdate      // provide update feature only if props.unUpdate exists
                                          ? ()=>{this.setState({updating: true}); onUpdate()}
                                          : undefined
                                      }
                                      onSearch={!disableSearch ? text => text
                                          ? this.setState({search: text.toLowerCase()})
                                          : this.setState({search: undefined}) : undefined}
                                      updating={updating}
                                      selectionToolbar={selectionToolbar}
                />
                <div className={classes.tableWrapper}>
                    <Typography type="subheading" style={{opacity: toSort?1:0, transition: "opacity", transitionDuration: "1s",
                        textAlign:"center",width:"100%"}}>
                        Sorting ...
                    </Typography>
                    <Table className={classes.table}  style={{opacity: toSort?0.5:1, transition: "opacity", transitionDuration: "1s"}}>
                        <EnhancedTableHead
                            numSelected={numSelected}
                            order={order}
                            orderBy={orderBy}
                            onSelectAllClick={onSelect
                                ? () => {}    // just stub
                                : undefined} // undefined as flag
                            onRequestSort={this.handleRequestSort}
                            rowCount={dataLength}
                            columnData={this.props.columnData}
                        />
                        <TableBody>
                            {data.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map(row => {
                                const edit = editId === row.id
                                return (
                                    <TableRow
                                        key={row.id}
                                        style={edit
                                            ? {backgroundColor: "lightgray", color: "black"}
                                            : onStylizeRow ? onStylizeRow(row) : {}}
                                        className={pinned && pinned[row.id]? classes.highlight : undefined}
                                    >
                                        {onSelect && <TableCell padding="checkbox">
                                            <Checkbox checked={this.isSelected(row.id)} onClick={event => this.handleSelect(event, row)}/>
                                        </TableCell>}
                                        {this.props.columnData.map((column,i) =>
                                            <TableCell numeric={column.numeric} key={i}
                                                       padding={column.disablePadding?"dense":"default"}>
                                                {column.editable
                                                    ? edit
                                                        ? <TextField
                                                            autoFocus
                                                            defaultValue={""+row[column.id]}
                                                            style={{textAlignment: "right"}}
                                                            InputLabelProps={{
                                                                shrink: true,
                                                            }}
                                                            onBlur={()=>setTimeout((_id)=>this.state.editId===_id && this.setState({editId: undefined}),500, row.id)}
                                                            onKeyDown={(event)=>{
                                                                if(event.keyCode===27) this.setState({editId: undefined})
                                                                else if(event.keyCode===13) {
                                                                    if(onEdit) onEdit(column.id, row.id, event.target.value, ""+row[column.id])
                                                                    this.setState({editId: undefined})
                                                                }
                                                            }}
                                                        />
                                                        : <div onClick={()=>this.setState({editId:row.id})} style={{cursor:"pointer"}}>
                                                                <Tooltip
                                                                    title="Enter amount of coins you have"
                                                                    placement={'bottom-end'}
                                                                    enterDelay={300}>
                                                                    <div>{this.drawCell(column, row)}</div>
                                                                </Tooltip>
                                                            </div>
                                                    : this.drawCell(column, row)}</TableCell>)}
                                    </TableRow>
                                );
                            })}
                            {emptyRows > 0 && (
                                <TableRow style={{ height: 49 * emptyRows }}>
                                    <TableCell colSpan={6} />
                                </TableRow>
                            )}
                        </TableBody>
                        <TableFooter>
                            <TableRow>
                                <TablePagination
                                    rowsPerPageOptions = {rowsPerPageOptions?rowsPerPageOptions:[5,10,25,75,100]}
                                    count={data.length}
                                    rowsPerPage={rowsPerPage}
                                    page={page}
                                    onChangePage={(event, page) => this.props.onChangePage(page)}
                                    onChangeRowsPerPage={event => this.props.onChangeRowsPerPage(event.target.value)}
                                />
                            </TableRow>
                        </TableFooter>
                    </Table>
                </div>
            </Paper>
        );
    }
    drawCell = (column, row) => this.props.onDrawCell
        ? this.props.onDrawCell(column, row, row[column.id])
        : row[column.id]
}

EnhancedTable.propTypes = {
    classes: PropTypes.object.isRequired,
    columnData: PropTypes.array.isRequired,
    title: PropTypes.string.isRequired,
    subtitle: PropTypes.string,
    data: PropTypes.array.isRequired,
    orderBy: PropTypes.string,
    rowsPerPage: PropTypes.number,
    rowsPerPageOptions: PropTypes.array,
    order: PropTypes.string,
    onUpdate: PropTypes.func, // provide handler for click on update button in the table toolbar
    onEdit: PropTypes.func, // provide handler for editable fields
    onDrawCell: PropTypes.func,
    onSorted: PropTypes.func, // provide handler for clicks on column headers (just before sorting)
    onSelect: PropTypes.func, // dome row was selected/unselected
    selectionToolbar: PropTypes.func, // component that displayed when some rows checked
    selected: PropTypes.object, // {id: row, ...}
    onStylizeRow: PropTypes.func,
    pinned: PropTypes.object, // {id: row, ...}
    onChangeRowsPerPage: PropTypes.func.isRequired,
    onChangePage: PropTypes.func.isRequired,
    disableSearch: PropTypes.bool,
    onDrawTitle: PropTypes.func // provide if you wish display complex title instead of just text
};

export default withStyles(styles)(EnhancedTable);


