export const coinMarketCap = async (store) => {
    const json = await(await fetch("https://api.coinmarketcap.com/v1/ticker/?limit=10000", {
        method: "get",
    })).json()
    store.dispatch({
        type: "market",
        payload: {
            coins: json.map(coin => Object.entries(coin) // convert to Number fields started from 3 position
                .reduce((acc, [field, value], i) => ({...acc, [field]: i > 2 ? Number(value) : value}), {})),
            last_updated: Date.now(),
        }
    })
}

export const whatToMine = async (store, miner) => { // miner is {name=>url}
    const [name, url] = Object.entries(miner)[0]
    const json = await( await fetch("http://wttmproxy2.epizy.com/i.php?url="+encodeURIComponent(url.replace("coins?","coins.json?")), {
        method: "get",
        credentials: "include"
    })).json()
    store.dispatch({
        type: "whatToMine",
        payload:
            {
                [name]: {
                    coins: Object.entries(json.coins) // remap hash as tag => coin instead of name => coin
                        .reduce((acc, [name, coin], i) => ({
                            [coin.tag]: {...coin, name, index: (i+1)}, // to prevent rewriting top values
                            ...acc
                        }), {}),
                    last_updated: Date.now(),
                }
            }
    })
}



