/** Handler of my's changes after reducer **/
export default (store, action) => {
    if(!action.payload.selected &&
        !action.payload.page &&
        !action.payload.rowsPerPage &&
        !action.type === "my.miners" &&
        !action.type === "my" &&  // full update
        action.payload.lastUpdated === undefined) return // update only when lastUpdated modified

    const [root/*, type, subType*/] = action.type.split('.')
    const state = store.getState()[root]
    localStorage.setItem(root, JSON.stringify(state))
}



