import {saveAs} from 'file-saver'

export const save = (filename, text) =>
    saveAs(new Blob([text], {type: "text/plain;charset=utf-8"}), filename)

export const load = (file, callback) => {
    const fr = new FileReader()
    fr.onload = event => callback(event.target.result)   // onload fires after reading is complete
    fr.readAsText(file);    // begin reading
}



