import React, {Component} from 'react';
import {connect} from 'react-redux'
import Table from "./util/Table"
import PropTypes from 'prop-types';
import {withStyles} from 'material-ui/styles';
import Typography from 'material-ui/Typography';

class _CoinsTable extends Component {
    _columnData = [
        {id: 'name', numeric: false, disablePadding: false, label: 'Name'},
        {id: 'price_usd', numeric: true, disablePadding: true, label: 'Price, $'},
        {id: 'percent_change_1h', numeric: true, disablePadding: true, label: 'Change 1h, %'},
        {id: 'percent_change_24h', numeric: true, disablePadding: true, label: 'Change 24h, %'},
        {id: 'percent_change_7d', numeric: true, disablePadding: true, label: 'Change 7d, %'},
        {id: 'my', numeric: true, disablePadding: false, editable: true, label: 'Balance*'},
    ]

    constructor(props, context) {
        super(props, context)
        this.columnData = props.myMiners
            ? this._columnData.concat(Object.entries(props.myMiners)
                .filter(([,url])=>url) // filter undefined values
                .map(([miner]) => ({
                id: "miner_" + miner,
                numeric: true,
                disablePadding: false,
                label: "Miner " + miner
            })))
            : this._columnData
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.myMiners && nextProps.myMiners !== this.props.myMiners) {
            this.columnData = this._columnData.concat(Object.entries(nextProps.myMiners)
                .filter(([,url])=>url) // filter undefined values
                .map(([miner]) => ({
                    id: "miner_" + miner,
                    numeric: true,
                    disablePadding: false,
                    label: "Miner " + miner
                })))
        }
    }

    render = () => <div style={{margin: 15}}>
        <Typography style={{width: "100%"}} type="caption">* Balance is editable - click number and enter your coins
            (Enter - to end editing, coin will be added to "My coins", ESC - to cancel). All data stored only in local
            storage of your browser,
            all calculations execute in browser, <b>NO SERVER INVOLVED</b> (Application send JSON requests to <a
                href="http://coinmarketcap.com" rel="noopener noreferrer" target="_blank">coinmarketcap.com</a> directly
            from browser).</Typography>
        <Table
            title="Coins"
            subtitle={"Last updated: " + new Date(this.props.market.last_updated).toUTCString()}
            columnData={this.columnData}
            data={this.props.market.coins
                ? this.props.market.coins.map(coin =>
                    ({
                        ...coin,
                        my: this.props.myCoins[coin.id] !== undefined ? this.props.myCoins[coin.id].balance : 0,
                        ...(this.props.myMiners ? Object.keys(this.props.myMiners).reduce((acc, miner) => ({
                            ...acc,
                            ["miner_" + miner]: this.props.whatToMine && this.props.whatToMine[miner] && this.props.whatToMine[miner].coins[coin.symbol]
                                ? this.props.whatToMine[miner].coins[coin.symbol].index
                                : 99999999
                        }), {}):{})
                    }))
                : []}
            pinned={this.props.pinned}
            orderBy={this.props.orderBy}
            order={this.props.order}
            onUpdate={this.props.onUpdate}
            onEdit={(columnId, rowId, value, oldValue) => columnId === "my" && this.props.saveBalance(rowId, value)}
            onDrawCell={(column, row, value) => {
                if (column.id === "name") return <a href={"https://coinmarketcap.com/currencies/" + row.id + "/#charts"}
                                                    target="_blank" style={{display: "flex", alignItems: "center"}}>
                    <img src={"https://files.coinmarketcap.com/static/img/coins/32x32/" + row.id + ".png"}
                         alt={row[column.id] + " logo"} style={{marginRight: 15}}/>{value}</a>
                if (column.id !== "my" && (value + "").indexOf(".") > 0) value = value.toFixed(1)
                if (column.id.startsWith("percent")) {
                    if (row[column.id] > 0)
                        return <div
                            style={{color: "green", fontWeight: "bold", fontSize: (100 + row[column.id] / 50) + "%"}}>
                            {value}</div>
                    if (row[column.id] < 0)
                        return <div
                            style={{color: "red", fontWeight: "bold", fontSize: (100 - row[column.id] / 50) + "%"}}>
                            {value}</div>
                }
                if (column.id.startsWith("miner")) {
                    if(value === 99999999) return "---"
                    const wtm = this.props.whatToMine[column.id.replace("miner_","")]
                    if(!wtm) return "n/a"
                    const wCoin = wtm.coins[row.symbol]
                    if(!wCoin) return "n/a"
                    return "$"+(wCoin.estimated_rewards*row.price_usd).toFixed(2)
                }
                return value
            }}
            onSorted={({orderBy, order}) => this.props.saveSort(orderBy, order)}
            //onStylizeRow={(row) => console.log(this.props.my.coins[row.id]?{backgroundColor: "lightblue"}:{})}
            page={this.props.page} rowsPerPage={this.props.rowsPerPage}
            onChangePage={this.props.onChangePage}
            onChangeRowsPerPage={this.props.onChangeRowsPerPage}
        />
    </div>
}

const styles = theme => ({})

_CoinsTable.propTypes = {
    classes: PropTypes.object.isRequired,
    onUpdate: PropTypes.func.isRequired, // provide handler for click on update button in the table toolbar
}


export default connect(
    state => ({
        market: state.market,
        myCoins: state.my.coins,
        myMiners: state.my.miners,
        order: state.mainBoard.order,
        orderBy: state.mainBoard.orderBy,
        page: state.mainBoard.page,
        rowsPerPage: state.mainBoard.rowsPerPage,
        pinned: state.my.selected,
        whatToMine: state.whatToMine,
    }),
    dispatch => ({
        saveBalance: (coinName, balance) => {
            if (balance.match(/^[\d.]+$/) === -1) return false

            dispatch({
                type: "my.coins." + coinName,
                payload: {balance: Number(balance)}
            })
            dispatch({
                type: "my",
                payload: {lastUpdated: Date.now()}
            })
            return true
        },
        saveSort: (orderBy, order) => dispatch({
            type: "mainBoard",
            payload: {orderBy, order}
        }),
        onChangePage: page => dispatch({
            type: "mainBoard",
            payload: {page}
        }),
        onChangeRowsPerPage: rowsPerPage => dispatch({
            type: "mainBoard",
            payload: {rowsPerPage}
        }),
    })
)(withStyles(styles)(_CoinsTable))



