import React, {Component} from 'react';
import {connect} from 'react-redux'
import Table from "./util/Table"
//import PropTypes from 'prop-types';
import IconButton from 'material-ui/IconButton';
import Tooltip from 'material-ui/Tooltip';
import DeleteIcon from 'material-ui-icons/Delete';
import AddIcon from 'material-ui-icons/Add';
import Typography from "material-ui/Typography"
import AddMinerPopover from './AddMinerPopover'

class _MyMinersTable extends Component {
    columnData = [
        {id: 'id', numeric: false, disablePadding: true, label: 'Name'},
    ]
    state = {
        selected: {},
        page: 0,
        rowsPerPage: 5,
        minerDialogAnchor: undefined,
    }
    render = () => <div style={{maxWidth: 1080, margin: 15}}>
        <Typography style={{width: "100%"}} type="caption">
            ** Add yours mining hardware, associated with preset at <a href="https://whattomine.com">https://whattomine.com</a>.<br/>
            After you set miners, you will see rewards for coins <b>$ per day</b>. Values sorted by position in list at whattomine.<br/>
            If you want more info like difficulty, feel free open whattomine page for yours hardware by clicking the name of miner.
        </Typography>
        <Table
            onDrawCell={(column, row, value) => <a href={this.props.miners[value]} target="_blank">{value}</a>}
            title="My mining hardware"
            subtitle=""
            columnData={this.columnData}
            data = { Object.keys(this.props.miners)
                .filter(id=>this.props.miners[id]!==undefined) // skip undefined
                .map(id => ({id: id}) )}
            order={this.props.minersOrder}
            orderBy={this.props.minersOrderBy}
            onSorted={({orderBy, order}) => this.props.saveSort(orderBy, order)} //
            selectionToolbar={(_props)=><Tooltip title="Delete">
                    <IconButton aria-label="Delete" onClick={() => this.props.deleteMiner(_props.selected)}>
                        <DeleteIcon />
                    </IconButton>
                </Tooltip>}
            onSelect={selected=>this.setState({selected})}
            selected={this.state.selected}
            page={this.state.page} rowsPerPage={this.state.rowsPerPage}
            onChangePage={(page)=>this.setState({page})}
            onChangeRowsPerPage={(rowsPerPage)=>this.setState({page: rowsPerPage})}
            disableSearch={true}
            onDrawTitle={title =>
                <div style={{display:"flex",alignItems:"center"}}>
                    <Typography type="title">{title}**</Typography>
                    <IconButton aria-label="Add" onClick={event => this.setState({minerDialogAnchor: event.currentTarget})}>
                        <AddIcon />
                    </IconButton>
                </div>}
        />
        {this.state.minerDialogAnchor && <AddMinerPopover
            anchorEl={this.state.minerDialogAnchor}
            open={true}
            onAdd={(name, url) => {
                if(name) this.props.editMiner(name, url)
                this.setState({minerDialogAnchor: undefined})
            }}
        />}
    </div>
}

export default connect(
    state => ({
        miners: state.my.miners, // {id => whatToMine_url}
        minersOrder: state.my.minersOrder,
        minersOrderBy: state.my.minersOrderBy,
    }),
    dispatch => ({
        deleteMiner: (selected) => {
            dispatch({ // delete
                type: "my.miners",
                payload: Object.keys(selected)
                    .reduce((acc, id) => ({ ...acc, [id]: undefined}), {})
            })
            return true
        },
        editMiner: (id, url) => dispatch({
            type: "my.miners",
            payload: {[id]: url}
        }),
        saveSort: (minersOrderBy, minersOrder) => dispatch({
            type: "my",
            payload: {minersOrderBy, minersOrder}
        }),
    })
)(_MyMinersTable)


