import React, {Component} from 'react';
import {Provider} from 'react-redux'
import {createStore, applyMiddleware, compose} from 'redux'
import saveMainBoard from './postMiddleware/mainBoard'
import saveMy from './postMiddleware/my'
import saveProvider from './postMiddleware/provider'
import {coinMarketCap, whatToMine} from './providers'
import Typography from 'material-ui/Typography';
import Status from './Status';
import './App.css';
import CoinsTable from "./CoinsTable"
import MyCoinsTable from "./MyCoinsTable"
import MyMinersTable from "./MyMinersTable"
import MyStore from "./MyStore"
import {load, save} from "./backup"

const STORAGE_VERSION = '38'
const BAD_RETRY_TIMEOUT = 30000
const GOOD_RETRY_TIMEOUT = 60000

const old = localStorage.getItem("VERSION")
const newVersionInstalled = old !== null && old !== STORAGE_VERSION
if (newVersionInstalled) {
    console.log("localStorage: New version '" + STORAGE_VERSION + "', old version '" + old + "': clear storage")
    localStorage.setItem("VERSION", STORAGE_VERSION)
}

class App extends Component {
    constructor(props) {
        super(props)
        const initialState = {   // generating an Initial State for store
            mainBoard: this.initialState("mainBoard"),
            market: this.initialState("market"),
            whatToMine: this.initialState("whatToMine"),
            my: this.initialState("my"),
        }
        this.mainBoardStore = createStore(this.reducer,
            initialState,
            window.__REDUX_DEVTOOLS_EXTENSION__ // add middleware, injected by redux-devtools browser's extension
                ? compose(applyMiddleware(this.middleware), window.__REDUX_DEVTOOLS_EXTENSION__())
                : applyMiddleware(this.middleware))
        this.updateProviders()
    }

    initialState = (root) => { // initial state for concrete root of our state, we have multiple roots
        const loadItem = (name, defaultValue = {}) => { // silently try load item from local storage
            try {
                const data = localStorage.getItem(name)
                const v = typeof(defaultValue) === 'object' ? JSON.parse(data) : data
                return v ? v : defaultValue
            } catch (e) {
                console.error(e)
                return defaultValue
            }
        }
        // here we fill only minimum fields that needed by app
        switch (root) {
            case "mainBoard":
                return loadItem(root, {
                    orderBy: "price_usd",
                    order: "desc",
                    rowsPerPage: 25,
                })
            case "my":
                return loadItem(root, {
                    coins: {},
                    orderBy: "balance_usd",
                    order: "desc",
                    miners: {},
                })
            case "market":
                return loadItem(root, {coins: [], last_updated: 0})
            case "whatToMine":
                return loadItem(root, {})
            default:
                return {}
        }
    }
    reducer = function (state = {}, action) {   // RAW. MAY PRODUCE KEYS WITH 'UNDEFINED', just handle by self
        if (action.type.startsWith("@@")) return state
        if (action.type === "market") return {
            ...state,
            market: action.payload,
        }
        const [root, type, subType] = action.type.split('.')
        const state2 = state[root] ? state[root] : {}    // child state, level 2
        const newState = type
            ? subType                     // state[type] is hash?
                ? {
                    ...state2, [type]: {
                        ...state2[type],
                        [subType]: action.payload
                            ? {...state2[type] ? state2[type][subType] : {}, ...action.payload}
                            : undefined
                    }
                }
                : {
                    ...state2, [type]: typeof(action.payload) === 'object'
                        ? {...state2[type], ...action.payload}
                        : action.payload
                } // 2 level - primitive type
            : action.payload && typeof(action.payload) === 'object'
                ? {...state2, ...action.payload}
                : action.payload // 1 level - primitive type
        return {...state, [root]: newState}           // 1 level
    }
    preMiddleware = (action) => {
    } // catching an actions did by backend at responses
    middleware = store => next => action => {
        this.preMiddleware(action)
        const result = next(action)                // to process reducer()
        this.postMiddleware(action)
        return result
    }
    postMiddleware = (action) => {
        const [root] = action.type.split('.')
        if (root === "mainBoard") saveMainBoard(this.mainBoardStore, action) // save sub-states of boards as whole object
        if (root === "market" || root === "whatToMine") saveProvider(this.mainBoardStore, action)
        if (root === "my") saveMy(this.mainBoardStore, action) // save sub-states of boards as whole object
    }
    updateProviders = () => {
        this.providersTask = undefined
        this.mainBoardStore.dispatch({
            type: "error",
            payload: undefined
        })
        coinMarketCap(this.mainBoardStore).then(
            () => this.handleProviderSuccess("market"),
            (error) => this.handleProviderError("market",error))
        const miners = this.mainBoardStore.getState().my.miners
        if(miners)
            Object.entries(miners).forEach(
                ([name,url])=>{
                    if(url) whatToMine(this.mainBoardStore, {[name]: url}).then(
                        () => this.handleProviderSuccess("whatToMine["+name+"]"),
                        (error) => this.handleProviderError("whatToMine["+name+"]",error))
                })
    }
    providersTask = undefined // setTimeout used to retry providers data from Internet
    setProvidersTimeout(timeout) {
        if(!this.providersTask || this.providersTask.timeout>timeout) {
            if(this.providersTask) clearTimeout(this.providersTask.handler)
            this.providersTask = {
                timeout,
                handler: setTimeout(this.updateProviders, timeout)
            }
        }
    }
    handleProviderError = (provider, error) => {
        this.mainBoardStore.dispatch({
            type: 'error',
            payload: {message: provider+": "+error.message, willResolveAt: Date.now() + BAD_RETRY_TIMEOUT}
        })
        this.setProvidersTimeout(BAD_RETRY_TIMEOUT)
    }
    handleProviderSuccess = (provider) => this.setProvidersTimeout(GOOD_RETRY_TIMEOUT)
    render() {
        return <div>
            <Provider store={this.mainBoardStore}>
                <div>
                    <div style={{display: "flex"}}>
                        <CoinsTable onUpdate={this.updateProviders}/>
                        <div>
                            <MyCoinsTable/>
                            <MyMinersTable/>
                            <MyStore
                                onSave={() => save("mycoins_json.txt", JSON.stringify({
                                    my: this.mainBoardStore.getState().my,
                                    mainBoard: this.mainBoardStore.getState().mainBoard,
                                }, null, "\t").replace(/\n/g, "\r\n"))}
                                onLoad={() => this.inputFile.click()}/>
                            <input style={{display: "none"}}
                                   type="file"
                                   ref={ref => this.inputFile = ref}
                                   onChange={event => load(event.target.files[0], (text) => {
                                       try {
                                           const js = JSON.parse(text)
                                           this.mainBoardStore.dispatch({
                                               type: "my",
                                               payload: js.my
                                           })
                                           this.mainBoardStore.dispatch({
                                               type: "mainBoard",
                                               payload: js.mainBoard
                                           })
                                       } catch (err) {
                                           alert("This files contains wrong data, may be you mistaken with name of the file")
                                       }
                                   })}/>
                        </div>
                    </div>
                    <Status/>
                </div>
            </Provider>
            <Typography style={{textAlign: "center"}}>
                Based on data from <a href="https://coinmarketcap.com/">https://coinmarketcap.com/</a>
            </Typography>
        </div>
    }
}

export default App;



