import React, {Component} from 'react';
import {connect} from 'react-redux'
import Table from "./util/Table"
//import PropTypes from 'prop-types';
import IconButton from 'material-ui/IconButton';
import Tooltip from 'material-ui/Tooltip';
import DeleteIcon from 'material-ui-icons/Delete';
import Typography from "material-ui/Typography"

class _MyCoinsTable extends Component {
    columnData = [
        {id: 'name', numeric: false, disablePadding: false, label: 'Name'},
        {id: 'balance', numeric: true, disablePadding: true, editable: true, color: true, label: 'Balance*'},
        {id: 'balance_usd', numeric: true, disablePadding: true, color: true, label: 'Balance, $'},
    ]
    sumUSD = 0
    render = () => <div style={{maxWidth: 1080, margin: 15}}>
        <Typography style={{width: "100%"}} type="caption">* Balance is editable - click number and enter your
            coins</Typography>
        <Table
            title="My coins"
            subtitle=""
            columnData={this.columnData}
            data={this.props.market.coins
                ? (this.sumUSD = 0) || this.props.market.coins
                    .filter((coin) => this.props.my.coins[coin.id]) //  only coins that we have
                    .map(coin => {
                        const data =  {
                            ...coin,
                            balance: this.props.my.coins[coin.id].balance,
                            balance_usd: this.props.my.coins[coin.id].balance * coin.price_usd,
                            img: "https://files.coinmarketcap.com/static/img/coins/32x32/" + coin.id + ".png"
                        }
                        this.sumUSD += data.balance_usd
                        return data
                    })
                : []}
            orderBy={this.props.my.orderBy}
            order={this.props.my.order}
            onEdit={(columnId, rowId, value, oldValue) => columnId === "balance" && this.props.saveBalance(rowId, value)}
            onDrawCell={(column, row, value) =>
                column.id === "name"
                    ? <a href={"https://coinmarketcap.com/currencies/" + row.id + "/#charts"} target="_blank"
                         style={{display: "flex", alignItems: "center"}}>
                        <img src={"https://files.coinmarketcap.com/static/img/coins/32x32/" + row.id + ".png"}
                             alt={row[column.id] + " logo"} style={{marginRight: 15}}/>{value}</a>
                    : (column.id === "balance_usd" ? value.toFixed(1) : value)
            }
            onSorted={({orderBy, order}) => this.props.saveSort(orderBy, order)} //
            selectionToolbar={() => <div style={{display: "flex", alignItems: "center"}}><Tooltip title="Delete">
                <IconButton aria-label="Delete" onClick={() => this.props.delete(this.props.my.selected)}>
                    <DeleteIcon/>
                </IconButton></Tooltip>
                <Typography type="title"
                            style={{margin: 15, width: "100%", textAlign: "right"}}>${Math.round(this.sumUSD)}</Typography>
            </div>}
            onSelect={this.props.onSelect}
            selected={this.props.my.selected}
            page={this.props.my.page} rowsPerPage={this.props.my.rowsPerPage}
            onChangePage={this.props.onChangePage}
            onChangeRowsPerPage={this.props.onChangeRowsPerPage}
        />
    </div>
}

export default connect(
    state => ({
        market: state.market,
        my: state.my,
    }),
    dispatch => ({
        saveBalance: (coinName, balance) => {
            if (balance.match(/^[\d.]+$/) === -1) return false

            dispatch({
                type: "my.coins." + coinName,
                payload: {balance: Number(balance)}
            })
            dispatch({
                type: "my",
                payload: {lastUpdated: Date.now()}
            })
            return true
        },
        saveSort: (orderBy, order) => dispatch({
            type: "my",
            payload: {orderBy, order}
        }),
        onSelect: (selected) => dispatch({
            type: "my",
            payload: {selected}
        }),
        delete: (selected) => {
            dispatch({ // delete
                type: "my.coins",
                payload: Object.keys(selected)
                    .reduce((acc, id) => ({...acc, [id]: undefined}), {})
            })
            dispatch({ // deselect
                type: "my",
                payload: {"selected": undefined}
            })
            dispatch({
                type: "my",
                payload: {lastUpdated: Date.now()}
            })
            return true
        },
        onChangePage: page => dispatch({
            type: "my",
            payload: {page}
        }),
        onChangeRowsPerPage: rowsPerPage => dispatch({
            type: "my",
            payload: {rowsPerPage}
        }),
    })
)(_MyCoinsTable)


